/* -------------------------------------------------------------------------- */

.cookbook-articles .h6 {
  position: relative;
  display: block;
  padding-bottom: .5rem;
  margin-bottom: .5rem;
}

.cookbook-articles .h6:after {
  position: absolute;
  bottom: -.125rem;
  left: 0;
  content: "";
  height: 4px;
  background: #f0c674;
  width: 2rem;
}




